<template>
  <div id="map"></div>
</template>
<script>
import mapboxgl from 'mapbox-gl'
export default {
  name: 'mapboxgl',
  data () {
    return {
      map: {}
    }
  },
  mounted () {
    mapboxgl.accessToken = 'pk.eyJ1IjoicXVvcmF1ayIsImEiOiJjamUxcHNxdzExbmphMnhwOW8zMHV5ZmQxIn0.zJdXQSrVdgmYsg9xitYEzw'
    var map = new mapboxgl.Map({
      container: 'map',
      style: 'mapbox://styles/mapbox/streets-v9',
      center: [-2, 55],
      zoom: 5
    })
    map.on('load', () => {
      map.addSource('communities', {'type': 'geojson', data: `${process.env.api}/layer`})
      map.addLayer({
        'id': 'communities',
        'type': 'circle',
        'source': 'communities',
        'paint': {
          'circle-radius': 10,
          'circle-opacity': 1,
          'circle-color': '#000000'
        }
      })
    })
    map.on('click', 'communities', (e) => {
      console.log(e.features[0].properties)
      this.$emit('scene_clicked', e.features[0].properties)
    })
    map.on('mouseenter', 'communities', function () {
      map.getCanvas().style.cursor = 'pointer'
    })

    // Change it back to a pointer when it leaves.
    map.on('mouseleave', 'communities', function () {
      map.getCanvas().style.cursor = ''
    })
    this.map = map
  }
}
</script>
<style>
#map {
}
