<template>
  <div>
    <i class="el-icon-close" @click="$emit('close')"></i>
    <h1>{{ scene.name }}</h1>
    <h2>Contact</h2>
    <div v-if="scene.contactPoint">
      <div v-if="scene.contactPoint">twitter: {{ contactPoint.twitter }}</div>
    </div>
    <h2>Games</h2>
    <div v-if="scene.games">
      <div v-for="game in games" :key="game.name"> {{ game.name }} </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'scene',
  props: ['scene'],
  computed: {
    contactPoint () {
      return JSON.parse(this.scene.contactPoint)
    },
    games () {
      return JSON.parse(this.scene.games)
    }
  }
}
</script>
<style>
.el-icon-close {
  cursor: pointer;
}
</style>



